<template>
	<div class="model">
       <div class="movie">
        	<video ref="video" preload="metadate">
        		<source src="./static/video/sex_model.mp4"  type="video/mp4">
        	</video>
        </div>
        	<img class="sex" src="../assets/sex.jpg">
        	<img class="sexno" src="../assets/sexno.png">
        	<v-touch @tap="showvideo"  class="btnShow"></v-touch>
        	<img class="sexfont" src="../assets/sexfont.png">
        	<img class="sexcontact" src="../assets/sexcontact.png" >       	
	</div>
</template>
<script>
	export default{
		methods:{
			showvideo(){
              this.$refs.video.play();             
             }
		}
	}
</script>
<style lang="scss">
  .sex{
  	 width:10rem;
  	 height:16.25rem;
  	 position:relative;
  	 opacity: 0;
  	 animation:showBg 1.8s ease  forwards;
  }	
  .sexno{
     position:relative;
     top:-4.8rem;
     z-index:1;
     width:100%;
     height:4.8125rem;
     opacity: 0;
     animation:fadeInUp 2.5s ease 0.5s forwards;
  }
  .sexfont{
  	position:relative;
  	top:-18.5rem;
  	left:-1.3rem;
  	z-index:2;
  	width:4.05rem;
  	height:auto;
  	opacity: 0;
  	animation:flipInX 1.8s ease 0.1s forwards;
  }
  .sexcontact{
  	position:relative;
  	top:-9rem;
  	left:1rem;
  	z-index:10;
  	width:2.6375rem;
  	height:auto;
  	 opacity: 0;
  	animation:showBg 1.8s ease  forwards;
  }
</style>


