<template>
  <div class="onewrap">
    <div class="movie">
      <video  ref="video" preload="metadate">
        <source src="./static/video/zhuchi.mp4"  type="video/mp4">
      </video>
    </div>
    <div class="onewin">
      <img  class="host " src="../assets/host.png">
      <v-touch @tap="showvideo" class="moveShow"></v-touch>
      <img class="font" src="../assets/font.png">   
    </div>
   </div>
</template>

<script>
export default {
  methods:{
    showvideo(){
       this.$refs.video.play();      
    }
  }
}
</script>
<style scoped lang="scss" scoped>
 
  .moveShow{
    background:url("../assets/btn.png") no-repeat center /100%;
    opacity:0;
    width:2.5rem;
    height:2.5rem;
    z-index:9999;
    position:absolute;
    top:6.3rem;
    left:4rem;
    animation: show 2s ease 2.5s forwards;
  }
   .onewin{
    position:relative;

    width:10rem;
    height:16.25rem;
    overflow: hidden;
    opacity:0;
    background:url("../assets/onewinz.jpg") no-repeat center /100% 100% ;
     animation:showBg 5s ease 0.1s forwards;
      .host{
        position:relative;
        top:3.8rem;
        width:6.55rem;
        left:1.528rem;
        height:auto;
        left:3.5rem;
        animation:hostshow 1.8s ease 0.2s forwards;
      }
      .font{
        width:6.9875rem;
        height:auto;
        position:relative;
        z-index:999;
        top:-5.4rem;
        animation:wobble 1.5s ease 1s forwards;
        opacity: 0;
      }

   }

   
 
   @keyframes hostshow{
     0%{
        top:3.8rem;
        left:3.5rem;
     }
     100%{
        top:3.8rem;
        left:1.4rem;
     }
   }
</style>



