<template>
    <div class="model">
		 <div class="movie">
	        	<video ref="video" preload="metadate">
	        		<source src="./static/video/ladies_model.mp4"  type="video/mp4">
	        	</video>
	     </div>
         <img class="ladies" src="../assets/ladies.jpg">
         <v-touch @tap="showvideo"  class="btnShow"></v-touch>
         <img class="lafont" src="../assets/lafont.png">
    </div>
</template>
<script>
	export default{
		methods:{
			showvideo(){
              this.$refs.video.play();             
             }
		}
	}
</script>
<style lang="scss">
	.ladies{
		width:10rem;
		height:16.25rem;
		opacity: 0;
        animation:showBg 1.8s ease  forwards;
	}
	.lafont{
		position:relative;
		top:-4.5rem;
		left:-0.65rem;
		width:5.6rem;
		height:auto;
		animation:bounceInLeft 2.2s ease forwards;
	}
</style>



