<template>
	<div class="model">
        <div class="movie">
        	<video ref="video" preload="metadate">
        		<source src="./static/video/lolita_model.mp4"  type="video/mp4">
        	</video>
        </div>	
        <img  class="lolita" src="../assets/lolita_model.jpg">
        <v-touch @tap="showvideo"  class="btnShow"></v-touch>
        <img class="lofont" src="../assets/lofont.png">

	</div>
</template>
<script>
	export default{
		methods:{
			showvideo(){
              this.$refs.video.play();             
             }
		}
	}
</script>
<style lang="scss">
    .model{
    	background:#000;
    	width:10rem;
    	height:16.25rem;
    }
	.lolita{
      width:10rem;
      height:16.25rem;
      overflow: hidden;
      opacity: 0;
      animation:showBg 1.8s ease  forwards;
	}
	
    .lofont{
    	width:6.5rem;
    	height:auto;
    	position:absolute;
    	top:11.8rem;
        left:0.6rem;
        animation:rubberBand 2s ease 0.5s forwards;
    }
</style>




