import Vue from 'vue'
import Router from 'vue-router'
import onewin from '@/components/onewin'
import lolita_model from '@/components/lolita_model' //萝莉
import queen_model from '@/components/queen_model' //女王
import sex_model from '@/components/sex_model' //性感
import ladies_model from '@/components/ladies_model' //淑女
Vue.use(Router)

export default new Router({
	mode: "history",
	base: __dirname,
	routes: [{
		path: '/',
		component: onewin
	}, {
		path: "/lolita",
		component: lolita_model
	}, {
		path: "/queen",
		component: queen_model
	}, {
		path: "/sex",
		component: sex_model
	}, {
		path: '/ladies',
		component: ladies_model
	}]
})


