<template>
	<div class="model">
		<div class="movie">
        	<video ref="video" preload="metadate">
        		<source src="./static/video/queen_model.mp4"  type="video/mp4">
        	</video>
        </div>
	     <img class="queenshow" src="../assets/queen.jpg" >
		 <img  class="queenbg" src="../assets/queenbg.png">
	     <v-touch @tap="showvideo" class="btnShow"></v-touch>
	     <img class="qufont " src="../assets/qufont.png">
	</div>
</template>
<script>
	export default {
	   methods:{
	     showvideo(){
           this.$refs.video.play();      
         }
	   }
	}
</script>


<style lang="scss">
   .queenshow{
   	  position:relative;
   	  left:3.5rem;
   	  width:10rem;
   	  height:16.25rem;
   	  z-index:1;
   	  animation:queenshow 1.5s ease forwards;
   }
   .queenbg{
   	 position:relative;
   	 width:10rem;
   	 height:16rem;
   	 z-index:2;
   	 top:-16.25rem;
   	 opacity: 0;
   	 animation:showBg 1.8s ease  forwards;
   }
   .qufont{
   	 position:relative;
   	 z-index:3;
     top:-30.5rem;
     left:-3rem;
     width:3.3375rem;
     height:auto;
     opacity: 0;
     animation:bounceIn 2s ease 1.2s forwards;
   }
   @keyframes queenshow{
   	 0%{
   	 	left:3.5rem;
   	 }
   	 100%{
   	 	left:0rem;
   	 }
   }
</style>


